import { useCallback, useState } from "react"
import { ShareLinkObject } from "@rp/common/utils"
import { shareCustomerIo } from "@rp/react/common/customer-io"

export const useCopyShareLink = () => {
  const [hasBeenCopied, setHasBeenCopied] = useState(false)
  const copy = useCallback(async (props: ShareLinkObject | undefined) => {
    if (!window.navigator.clipboard) return
    if (!props) return
    setHasBeenCopied(true)
    setTimeout(() => setHasBeenCopied(false), 1000)
    shareCustomerIo.linkGenerated(props)
    return window.navigator.clipboard.writeText(props.url)
  }, [])

  return {
    hasBeenCopied,
    copy,
  }
}
